main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

main h3 {
    margin: 1rem 0;
}

/* */
div.logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
}
div.logo img {
    margin-right: 1.5rem;
}
div.logo h1 {
    color: #ffe66e;
}

/* */
div.collection img {
    border: 4px solid #ffe66e;
}

/* */
h3 {
    font-weight: normal;
}

main div.content {
    text-align: left;
    text-transform: uppercase;
    font-weight: normal;
    font-size: 1.2rem;
    color: #ffe66e;
}

main div.content p {
    margin: 0;
    padding: 0;
}